<template>
  <div class="login-container">
    <el-form
      :model="loginData"
      :rules="rules2"
      status-icon
      ref="loginData"
      label-position="left"
      class="login-page"
      label-width="100px"
    >
      <h3 class="title">物料追踪系统-{{ formTitle }}</h3>

      <!-- <el-option
              v-for="(item, index) in ProductIdNameList"
              :key="index + ' search'"
              :label="item.product_name"
              :value="item.product_id"
            ></el-option> -->

      <el-form-item label="项目" prop="product_id">
        <el-select
          v-model="loginData.product_id"
          placeholder="请选择"
          style="width: 100%"
          clearable
          @change="getWorkCodeNameSearchList"
        >
          <el-option
            v-for="(item, index) in ProductIdNameList"
            :key="index + ' search'"
            :label="item.product_name"
            :value="item.product_id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label="工站"
        prop="work_id"
        v-if="formTitle == '用户端' && loginData.product_id"
      >
        <el-select
          v-model="loginData.work_id"
          placeholder="请选择"
          style="width: 100%"
        >
          <el-option
            v-for="(item, index) in WorkCodeNameSearchList"
            :key="index + ' search'"
            :label="item.work_name"
            :value="item.work_id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="用户名" prop="user">
        <el-input
          type="text"
          v-model="loginData.user"
          auto-complete="off"
          placeholder="用户名"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          type="password"
          v-model="loginData.password"
          auto-complete="off"
          placeholder="密码"
        ></el-input>
      </el-form-item>
      <el-checkbox v-model="checked" class="rememberme">记住密码</el-checkbox>
      <el-form-item style="width: 100%">
        <el-button
          type="primary"
          style="width: 100%"
          @click="handleSubmit"
          :loading="logining"
          >登录</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { loginApi } from "@/request/api";
import { mapState, mapActions } from "vuex";
export default {
  props: {
    toUrl: {
      type: String,
      default: "/",
    },
    formTitle: {
      type: String,
      default: "",
    },
  },

  computed: {
    ...mapState([
      "ProductIdNameList",
      "WorkCodeNameSearchList",
      "CurrentUserInfo",
    ]),
  },
  data() {
    return {
      logining: false,
      loginData: {
        product_id: "",
        work_id: "",
        user: "",
        password: "",
      },
      rules2: {
        product_id: [
        ],
        work_id: [{ required: true, message: "请选择工站", trigger: "change" }],
        user: [{ required: true, message: "请输入用户名", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
      checked: false,
    };
  },
  methods: {
    ...mapActions([
      "getLogisticGetProductId",
      "getLogisticProjectSearchWorkcode",
      "getLogisticCurrentUserInfo",
    ]),

    getWorkCodeNameSearchList() {
      console.log(123);
      if (this.formTitle == "用户端") {
        this.getLogisticProjectSearchWorkcode({
          product_id: this.loginData.product_id,
        });
      } else {
      }
    },
    handleSubmit() {
      console.log(this.loginData);

      this.$refs.loginData.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.formTitle == "用户端") {
            loginApi.getLogisticLogin(this.loginData).then((res) => {
              console.log("登录信息", res);
              if (res && res.code == 0) {
                this.getLogisticCurrentUserInfo();
                loginApi.getLogisticCurrentUserInfo().then((userRes) => {
                  console.log("当前登陆人的接口 userRes", userRes);

                  if (userRes.code == 0) {
                    if (userRes.data.work_type == "加工") {
                      this.$router.push({ path: "/userUrl/MissStation" });
                    } else if (this.CurrentUserInfo.work_type == "维修") {
                      this.$router.push({ path: "/userUrl/MissStationM" });
                    } else if (this.CurrentUserInfo.work_type == "检验") {
                      this.$router.push({ path: "/userUrl/MissStationIPQC" });
                    } else if (this.CurrentUserInfo.work_type == "包装") {
                      this.$router.push({ path: "/userUrl/MissStationPack" });
                    } else if (this.CurrentUserInfo.work_type == "入库出库") {
                      this.$router.push({ path: "/userUrl/FinishedProductStorage" });
                    } else {
                      this.$router.push({ path: this.toUrl });
                    }
                  }
                });
                // this.$router.push({ path: this.toUrl });
              } else {
                this.$message({
                  message: res.message,
                  type: "error",
                });
              }
            });
          } else {
            loginApi
              .getLogisticManageLogisticLogin(this.loginData)
              .then((res) => {
                console.log("登录信息", res);
                if (res && res.code == 0) {
                  this.getLogisticCurrentUserInfo();
                  this.$router.push({ path: this.toUrl });
                } else {
                  this.$message({
                    message: res.message,
                    type: "error",
                  });
                }
              });
          }
        }
      });
    },
    async getProductIdNameList() {
      await this.getLogisticGetProductId();
    },
  },
  mounted() {
    this.getProductIdNameList();

    if (this.formTitle == "用户端") {
      this.rules2.product_id = [
        { required: true, message: "请选择项目", trigger: "change" },
      ];
    } else {
      this.rules2.product_id = [];
    }

    console.log(12336854);
    console.log(
      "this.CurrentUserInfo CurrentUserInfo CurrentUserInfo ",
      this.CurrentUserInfo
    );
  },
};
</script>

<style scoped lang="scss">
.login-container {
  width: 100%;
  height: 100%;
  background-color: lightblue;
  // background: url("../../assets/images/loginBg.png") no-repeat;
  background-size: cover;
  .title {
    width: 100%;
    text-align: center;
    font-size: 20px;
    line-height: 40px;
    margin-bottom: 20px;
  }
  .login-page {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    width: 400px;
    padding: 35px 35px 15px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 5px #cac6c6;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  label.el-checkbox.rememberme {
    margin: 0px 0px 15px;
    text-align: left;
  }
}
</style>